import React from "react";
import { Timeline as TimeLineReact, Events, createTheme, themes } from "@merc/react-timeline";

import Universidad from "./Events/Universidad";
import EnnovasoftInicio from "./Events/Ennovasoft/Inicio";
// import EnnovasoftTermino from './Events/Ennovasoft/Termino';
import Laboratorio1 from "./Events/Laboratorio/Laboratorio1";
import Laboratorio2 from "./Events/Laboratorio/Laboratorio2";
import PagosUniversidad from "./Events/PagosUniversidad";
import BlueRadio from "./Events/BlueRadio";
import Freelance from "./Events/Freelance";
import Neubox from "./Events/Neubox";
import TiempoLibre from "./Events/TiempoLibre";
const colors = require("tailwindcss/colors");

const TimeLine = () => {
  const primaryColor = colors.coolGray[500];
  const theme = createTheme(themes.default, {
    timelineTrack: {
      backgroundColor: primaryColor,
    },

    date: {
      backgroundColor: primaryColor,
    },

    marker: {
      borderColor: primaryColor,
    },
  });

  return (
    <div className="size__container">
      <TimeLineReact theme={theme}>
        <Events>
          <Universidad />

          <Laboratorio1 />
          <Laboratorio2 />

          <BlueRadio />

          <PagosUniversidad />

          <EnnovasoftInicio />
          {/* <EnnovasoftTermino /> */}

          <Freelance />

          <Neubox />

          {/* <TiempoLibre /> */}
        </Events>
      </TimeLineReact>
    </div>
  );
};

export default TimeLine;
