import "./styles.scss";
import React from "react";

import jhay from "../../../images/jacob.webp";

const Header = () => {
  function aniosPasados(fecha) {
    const hoy = new Date();
    const anios = new Date(fecha);
    let edad = hoy.getFullYear() - anios.getFullYear();
    const m = hoy.getMonth() - anios.getMonth();

    if (m < 0 || (m === 0 && hoy.getDate() < anios.getDate())) {
      edad--;
    }

    return " " + edad;
  }

  return (
    <div className="header__container">
      <div className="header__content size__container">
        <img src={jhay} alt="jonathan jacob verdugo" className="header__content__img" height="200" width="200" />

        <div className="name">
          <h1>Jonathan Jacob</h1>
          <h2>Ing. de Software</h2>
          <p className="font-sans text-lg max-w-xl leading-5 mt-3">
            Soy un desarrollador fullstack con mas de
            {aniosPasados("02-16-2018")} años de experiencia, orientado a tecnologías web, entusiasta en aprender nuevas tecnologías, frameworks,
            librerías, y con facilidad de adaptación a los cambios.
          </p>
        </div>
      </div>
    </div>
  );
};

export default Header;
