import React from "react";
import PropTypes from "prop-types";
import { arrowImg } from "../../Icons";
import RenderURL from "./components/RenderURL";
import { FaDotCircle } from "react-icons/fa";

const RenderText = ({ title }) => {
  return <>{title}</>;
};

const Links = ({ pages, title }) => {
  return (
    <ul className="pt-2">
      <li className="font-semibold pb-1">{title}</li>
      {pages.map((item, index) => (
        <li key={index} className="pl-2 pb-1" style={{ display: "flex", alignItems: "center", gap: "6px" }}>
          <FaDotCircle size={8} color="#aaa" />
          {item.link !== "" ? <RenderURL title={item.title} link={item.link} local={item.local} /> : item.title}
        </li>
      ))}
    </ul>
  );
};

Links.propTypes = {
  title: PropTypes.string.isRequired,
  pages: PropTypes.arrayOf(
    PropTypes.shape({
      link: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      local: PropTypes.bool,
    })
  ),
};

Links.defaultProps = {
  title: "",
  pages: [],
  local: false,
};

export default Links;
