import React from "react";
import PropTypes from "prop-types";

const RenderImage = ({ item }) => {
  let props = {
    loading: "lazy",
  };

  if (typeof item === "string") {
    props.src = item;
    props.alt = "icon";
  }

  if (item.src) {
    props.src = item.src;
  }

  if (item.alt) {
    props.alt = item.alt;
  }

  if (item.title) {
    props.title = item.title;
  }

  return <img {...props} />;
};

const Icons = ({ icons }) => {
  return (
    <>
      {icons.map((item, index) => (
        <RenderImage item={item} key={index} alt={index} loading="lazy" />
      ))}
    </>
  );
};

Icons.propTypes = {
  icons: PropTypes.array.isRequired,
};

export default Icons;
